import React from 'react';
import { useSelector } from 'react-redux';
import styled from 'styled-components';
import AppQuizFooter from './AppQuizFooter';
import AppQuizHeader from './AppQuizHeader';
import AppQuizItem from './AppQuizItem';
import AppQuizTitle from './AppQuizTitle';

const Container = styled.div`
    display: flex;
    flex-direction: column;
    padding: 50px 20px;
`

export default function AppQuiz() {

    const { questions } = useSelector(state => state.quiz);


    return (
        <Container>
            <AppQuizTitle />
            <AppQuizHeader />
            {questions?.map(item => <AppQuizItem key={item._id} question={item}/>)}
            <AppQuizFooter />
        </Container>
    )
}
