import React, { useState } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import styled from 'styled-components';
import { colors } from '../../assets/style';
import { SET_QUIZ } from '../../store/quiz/actions';

const PageNumber = styled.span`
    color: ${props => props.active && colors.main.green};
    margin-right: 20px;
    font-size: 16px;
    cursor: pointer;
`
const Pages = styled.span`
    display: flex;
    align-items: center;
    height: 50px;
`

export default function AppQuizFooter() {

    const [active, setActive] = useState(0);

    const dispatch = useDispatch();

    const { pageCount } = useSelector(state => state.quiz);

    const goToPage = (id) => {
        dispatch(SET_QUIZ(id + 1));
    }

    return (
        <Pages>
            {[...Array(pageCount).keys()].map(
                item => <PageNumber
                    key={item}
                    active={active === item}
                    onClick={() => goToPage(item)}>
                    {item + 1}
                </PageNumber>
            )}
        </Pages>
    )
}
